export var getCardType = function (cardNumber) {
    var reVisa = new RegExp("^4");
    var reMasterCard = new RegExp("^5[1-5]|^222[1-9]|^22[3-9]|^2[3-6]");
    var reAmex = new RegExp("^3[47]");
    if (reVisa.test(cardNumber))
        return "visa";
    if (reMasterCard.test(cardNumber))
        return "mastercard";
    if (reAmex.test(cardNumber))
        return "amex";
    return "visa";
};
export var formatCVV = function (cvv) {
    return cvv.replace(/[0-9]/g, "*");
};
export var formatCreditCardNumber = function (cardNumber, cardType) {
    if (cardType === "amex") {
        for (var i = cardNumber.length; i < 15; i++) {
            cardNumber = cardNumber + "#";
        }
        cardNumber =
            cardNumber.substring(0, 4) +
                cardNumber.substring(4, 12).replace(/[0-9]/g, "*") +
                cardNumber.substring(12, 15);
    }
    else {
        for (var i = cardNumber.length; i < 16; i++) {
            cardNumber = cardNumber + "#";
        }
        cardNumber =
            cardNumber.substring(0, 4) +
                cardNumber.substring(4, 12).replace(/[0-9]/g, "*") +
                cardNumber.substring(12, 16);
    }
    return cardNumber;
};
