/** @jsx jsx */
import React from "react"
import PropTypes from "prop-types"
import { jsx, css } from "@emotion/core"

import { Typography, Colors } from "../../../../styles"
import { sectionStyles } from "../shared/styles"

const styles = {
  contentWrapper: css`
    display: flex;
    justify-item: space-between;
  `,
  projectWrapper: css`
    max-width: 27rem;
    padding: 0 3rem;
    border-radius: 0.25rem;
    margin-top: 0.5rem;
  `,
  projectImage: css`
    width: 100%;
    margin-bottom: 0.5rem;
    border-radius: 0.125rem;
    border: 1px solid ${Colors.grey90};
  `,
  projectTitle: css`
    ${Typography.gamma}
    font-weight: 600;
  `,
  projectRole: css`
    display: flex;
    justify-content: space-between;
    ${Typography.epsilon}
    font-weight: 600;
    margin-top: 0.5rem;
    margin-bottom: 0.625rem;
  `,
  projectDescription: css`
    ${Typography.body}
    margin-bottom: 0.75rem;
  `,
}

export const JobSection = (props) => {
  return (
    <section css={sectionStyles.section}>
      <div css={sectionStyles.sectionWrapper}>
        <h1 css={sectionStyles.sectionTitle}>{props.companyName}</h1>
        <h3 css={sectionStyles.sectionSubTitle}>{props.position}</h3>
        <div css={styles.contentWrapper}>
          {props.projects.map((project, index) => {
            return (
              <div css={styles.projectWrapper} key={index}>
                <a href={project.projectLink}>
                  <img
                    src={project.projectImage}
                    css={styles.projectImage}
                    alt={project.projectTitle}
                  />
                </a>
                <a href={project.projectLink} css={styles.projectTitle}>
                  {project.projectTitle}
                </a>
                <div css={styles.projectRole}>
                  <div>Role</div>
                  <div>{project.projectRole}</div>
                </div>
                {project.projectDescription
                  ? project.projectDescription.map((description, index) => {
                      return (
                        <div css={styles.projectDescription} key={index}>
                          {description}
                        </div>
                      )
                    })
                  : null}
              </div>
            )
          })}
        </div>
        <p>{props.experienceDescription}</p>
      </div>
      {props.lastItem ? null : <div css={sectionStyles.separatedLine} />}
    </section>
  )
}

JobSection.propTypes = {
  projects: PropTypes.object,
  companyName: PropTypes.string,
  position: PropTypes.string,
  lastItem: PropTypes.bool,
  experienceDescription: PropTypes.string,
}
